import './App.css';
import React, { useEffect, useState } from 'react';
import Clock from 'react-clock';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import 'react-clock/dist/Clock.css';
import copy_clipborad from './copy_clipborad.png';
import {Col,Row} from 'react-bootstrap';


function App() {
  // set const variables
  const [wcValue,wcSetValue] = useState(new Date());
  const [timeStamp,setTimeStamp] = useState(Math.round(wcValue.getTime()/1000));
  const [inputValue,setInputValue] = useState(Math.round(wcValue.getTime()/1000));
  const [timeStampResult,setTimeStampResult] = useState(false);
  const [gmtTimeStamp,setGmtTimeStamp] = useState("");
  const [localTimeStamp,setLocalTimeStamp] = useState("");
  const [relativeTimeStamp,setRelativeTimeStamp] = useState("");

  // set date to timestamp convert const
  const [dYear,setDYear] = useState(wcValue.getFullYear());
  const [dMonth,setDMonth] = useState(wcValue.getMonth() + 1);
  const [dDay,setDDay] = useState(wcValue.getDate());
  const [dHour,setDHour] = useState(wcValue.getHours() +1);
  const [dMinutes,setDMinutes] = useState(wcValue.getMinutes() +1);
  const [dSecound,setDSecound] = useState(wcValue.getSeconds() +1);
  const [dMilliSecound,setDMilliSecound] = useState(wcValue.getMilliseconds() +1);
  const [dateTimeResult,setDateTimeResult] = useState(false);
  const [usrDateTimeStamp,setUsrDateTimeStamp] = useState(Math.round(wcValue.getTime()/1000));
  const [gmtDateTime,setGmtDateTime] = useState("");
  const [localDateTime,setLocalDateTime] = useState("");
  const [relativeDateTime,setRelativeDateTime] = useState("");

  // set usereffect variables
  useEffect(() => {
    const interval = setInterval(() => {
      wcSetValue(new Date());
      setTimeStamp(Math.round(new Date()/1000));
    },1000);
    return () => {
      clearInterval(interval);
    };
  },[]);

  // render fun
  return (
    <div className="App">
      <div id='maincontainer'>
        <br />
        <header id='headerBox'>
          <h2 id="headerTitle">
            Unix Epoch Time Stamp Calculate Tool
          </h2>
          <p id="headerDescription">
            The <b>Unix Timestamp</b> elapsed since <b>January 1st, 1970 UTC</b>
          </p>
        </header>
        <br/>
        <div className='contentBody'>
          <div id='walkClock'>
            <br/>
            <center>
              <Clock value={wcValue} size={250} renderNumbers={true} minuteMarksLength={8} hourMarksWidth={3} secondHandWidth={2} minuteHandWidth={3} hourHandWidth={5} hourMarksLength={12} />
              <p id="timestampShowTitle">
                The Current Unix Epoch Time
              </p>
              <h2 id="timestampShow">
                {timeStamp}
                <CopyToClipboard text={timeStamp}>
                  <button id="copyClipBorad"><img  height="25px" width="15px" alt='copy clipboard' src={copy_clipborad} /></button>
                </CopyToClipboard>
              </h2>
              <Row id="timeStringShow">
                <Col>
                  {wcValue.toGMTString()}
                </Col>
                <Col>
                  {wcValue.toString()}
                </Col>
              </Row>
            </center>
            <br/>
          </div>
          {/* &emsp;&emsp;&emsp;&emsp;&emsp; */}
          <div id="timeStampToDateBox">
            <b>Enter A TimeStamp</b><br/>
            <input type="number" min="0" name='inputTimeStamp' id="inputTimeStamp" placeholder={inputValue} onChange={(e) => setInputValue(e.target.value) } maxLength="20"/><br/>
            <input type="button" name='inputTimeStampConvert' onClick={()=>timeStampToDateFomart(inputValue,timeStamp,setTimeStampResult,setGmtTimeStamp,setLocalTimeStamp,setRelativeTimeStamp)} value="Convert" id="inputTimeStampConvert"/>&emsp;&emsp;
            <input type="button" name='inputTimeStampClear' value="Clear" onClick={()=>clearInputBox(setTimeStampResult)} id="inputTimeStampClear"/><br/>
            <div id="timeStampToStringResult">
              <center>
                { timeStampResult ? 
                  <table id="table_timeStampResult" >
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>GMT</td>
                      <td className='tr_td_padding'>{gmtTimeStamp}</td>
                    </tr>
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>Your local Zone</td>
                      <td className='tr_td_padding'>{localTimeStamp}</td>
                    </tr>
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>Relative</td>
                      <td className='tr_td_padding'>{relativeTimeStamp}</td>
                    </tr>
                  </table> 
                : null }
                </center>
            </div>
          </div>
          <div id="dateToTimeStampBox">
            <b>Enter A Date And Time</b><br/>
              <div className="dateToTimeStampInput">
                <label>Year</label><br/>
                <input className='dateToTimestampInputAttr' name="y" type="number" id="y" onChange={(e)=> setDYear(e.target.value)} step="1" maxLength="4" placeholder="YYYY" value={dYear} /> <b>-</b> 
              </div>
              <div className="dateToTimeStampInput">
                  <label>Month</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDMonth(e.target.value)} step="1" maxLength="2" placeholder="MM" value={dMonth} /> <b>-</b>
              </div>
              <div className="dateToTimeStampInput">
                  <label>Day</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDDay(e.target.value)} step="1" maxLength="2" placeholder="DD" value={dDay} /> <b>-</b>
              </div>
              <div className="dateToTimeStampInput">
                  <label>Hour</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDHour(e.target.value)} step="1" maxLength="2" placeholder="HH" value={dHour} /> <b>:</b>
              </div>
              <div className="dateToTimeStampInput">
                  <label>Minutes</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDMinutes(e.target.value)} step="1" maxLength="2" placeholder="MM" value={dMinutes} /> <b>:</b>
              </div>
              <div className="dateToTimeStampInput">
                  <label>Seconds</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDSecound(e.target.value)} step="1" maxLength="2" placeholder="SS" value={dSecound} /> <b>:</b>
              </div>
              <div className="dateToTimeStampInput">
                  <label>Milliseconds</label><br/>
                  <input className='dateToTimestampInputAttr' name="m" type="number" id="y" onChange={(e)=> setDMilliSecound(e.target.value)} step="1" maxLength="2" placeholder="MM" value={dMilliSecound} />
              </div> <br/>  
              <input type="button" name='inputTimeStampConvert' onClick={()=>dateFomatToTimeStamp(dYear,dMonth,dDay,dHour,dMinutes,dSecound,dMilliSecound,setGmtDateTime,setLocalDateTime,setRelativeDateTime,timeStamp,setUsrDateTimeStamp,setDateTimeResult)} value="Convert" id="inputTimeStampConvert"/>&emsp;&emsp;
              <input type="button" name='inputTimeStampClear' value="Clear" onClick={()=>clearInputBox(setDateTimeResult)} id="inputTimeStampClear"/><br/>
              <div id="timeStampToStringResult">
              <center>
                { dateTimeResult ? 
                  <table id="table_timeStampResult" >
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>TimeStamp</td>
                      <td className='tr_td_padding'>{usrDateTimeStamp}</td>
                    </tr>
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>GMT</td>
                      <td className='tr_td_padding'>{gmtDateTime}</td>
                    </tr>
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>Your local Zone</td>
                      <td className='tr_td_padding'>{localDateTime}</td>
                    </tr>
                    <tr className='tr_td_padding'>
                      <td className='tr_td_padding'>Relative</td>
                      <td className='tr_td_padding'>{relativeDateTime}</td>
                    </tr>
                  </table> 
                : null }
                </center>
            </div>
          </div>
        </div>
        <br/>
        <footer id="footerBox">
          <p id="footerLinks">
            <a href='/'>Home</a> - <a href='/'>Privacy</a> - <a href='/'>Contact</a> - <a href='/'>Cookies</a>
          </p>
          <br/>
          <p id="footerCopyRight">
          Copyright © 2022 thavasi t. All rights reserved.
          </p>
        </footer>
        <br/>
      </div>
    </div>
  );
}

function dateFomatToTimeStamp(dYear,dMonth,dDay,dHour,dMinutes,dSecound,dMilliSecound,setGmtDateTime,setLocalDateTime,setRelativeDateTime,timeStamp,setUsrDateTimeStamp,setDateTimeResult){
  dMonth = dMonth - 1

  var dateTimeObject = new Date(dYear,dMonth,dDay,dHour,dMinutes,dSecound,dMilliSecound);
  setGmtDateTime(dateTimeObject.toGMTString());
  setLocalDateTime(dateTimeObject.toString());
  setUsrDateTimeStamp(dateTimeObject.getTime());
  var usrDateTime = dateTimeObject.getTime() / 1000;
  timeDifference(timeStamp,usrDateTime,setRelativeDateTime);
  setDateTimeResult(true);
}

function clearInputBox(setTimeStampResult){
  // document.getElementById("inputTimeStamp").value = "";
  setTimeStampResult(false);
}

function timeStampToDateFomart(usrTimeStamp,liveTimeStamp,setTimeStampResult,setGmtTimeStamp,setLocalTimeStamp,setRelativeTimeStamp){

  var gmtTimeStr = new Date(parseInt(usrTimeStamp) * 1000).toGMTString();
  var localTimeStr = new Date(parseInt(usrTimeStamp) * 1000).toString();
  setGmtTimeStamp(gmtTimeStr);
  setLocalTimeStamp(localTimeStr);
  timeDifference(liveTimeStamp,usrTimeStamp,setRelativeTimeStamp);
  setTimeStampResult(true);
}

function timeDifference(current, previous,setRelativeTimeStamp) {

  var msPerMinute = 60;
  var msPerHour = msPerMinute * 60;
  var msPerDay = msPerHour * 24;
  var msPerMonth = msPerDay * 30;
  var msPerYear = msPerDay * 365;
  var result = "";

  var elapsed = current - previous;
  elapsed = Math.abs(elapsed); 

  if (elapsed < msPerMinute) {
      result =  Math.round(elapsed) + ' seconds ago';   
  }
  else if (elapsed < msPerHour) {
      result =  Math.round(elapsed/msPerMinute) + ' minutes ago';   
  }
  else if (elapsed < msPerDay ) {
      result =  Math.round(elapsed/msPerHour ) + ' hours ago';   
  }
  else if (elapsed < msPerMonth) {
    result =  'approximately ' + Math.round(elapsed/msPerDay) + ' days ago';   
  }
  else if (elapsed < msPerYear) {
    result =  'approximately ' + Math.round(elapsed/msPerMonth) + ' months ago';   
  }
  else {
    result =  'approximately ' + Math.round(elapsed/msPerYear ) + ' years ago';   
  }
  setRelativeTimeStamp(result);
}

export default App;
